<template>
  <div class="row">
    <div class="col-3">
      <ul class="list-group">
        <li :class="{ 'list-group-item': true, 'active': item === currentItem }" 
            v-for="item in category" 
            @click="currentItem = item">{{item.name}}</li>
      </ul>
    </div>
    <div class="col-9">
      <carousel
      :autoplay="true"
      :perPage="1">
        <slide v-for="n in currentItem.photos">
          <div class="img-card">
            <img :src="`/static/${currentItem.name}/${n}.jpg`" alt="" >
          </div>
        </slide>
      </carousel>
    </div>
  </div>
</template>

<script>
import { Carousel, Slide } from 'vue-carousel';

export default {
  name: 'Main',
  components: {
    Carousel,
    Slide,
  },
  data() {
    return {
      currentItem: '',
      category: [
        {
          name: '壓克力製品',
          photos: 15,
        },
        {
          name: '各式活動展架',
          photos: 6,
        },
        {
          name: '大圖輸出',
          photos: 4,
        },
        {
          name: '戶外精緻招牌',
          photos: 22,
        },
        {
          name: '室內招牌',
          photos: 11,
        },
        {
          name: '不鏽鋼磁性胸牌',
          photos: 2,
        },
        {
          name: '拒馬製作',
          photos: 3,
        },
        {
          name: '各式精美公佈欄',
          photos: 6,
        },
        {
          name: '車體廣告',
          photos: 14,
        },
        {
          name: '大型廣告帆布製作',
          photos: 4,
        },
        {
          name: '電腦割字',
          photos: 6,
        },
        {
          name: '紅布條及各式旗面',
          photos: 3,
        },
        {
          name: '各式立體字',
          photos: 24,
        },
        {
          name: '隔熱紙及防爆模施作',
          photos: 4,
        },
      ],
    };
  },
  mounted() {
    this.currentItem = this.category[0];
  },
};
</script>

<style lang="sass">
.img-card
  margin: 3%
  padding: 1%;
  box-shadow: 0 16px 24px 2px rgba(0,0,0,.14), 0 6px 30px 5px rgba(0,0,0,.12), 0 8px 10px -5px rgba(0,0,0,.2);
  img
    width: 100%;
</style>



